<template>
<div class="container-fluid">
  <div class="row">
    <div class="col-sm-12" style="height:30px;"></div>
  </div>
  <div class="row">
    <div class="col-sm-4">

    </div>
    <div class="col-sm-4">
      <div class="card card-block form-section">
        <h2><center>登 录</center></h2>
        <fieldset class="form-group">
          <label for="merchant_mobile">手机号码</label>
          <input autofocus="autofocus" class="form-control" type="text" value="" name="mobile" v-model="mobile"  />
        </fieldset>
        <fieldset class="form-group">
          <label for="merchant_password">密码</label>
          <input autocomplete="off" class="form-control" type="password" name="password" v-model="password" />
        </fieldset>
        <button @click="login" class="btn btn-primary btn-lg btn-block">登 录</button>
      </div>
    </div>
    <div class="col-sm-4">

    </div>
  </div>

</div>

</template>

<script>
  import { signin, showAlert, showHandleTip } from '../vuex/actions'

  export default {
    vuex: {
      getters: {
        isLogged: state => state.merchant.isLogged
      },
      actions: {
        signin,
        showAlert,
        showHandleTip
      }
    },
    data () {
      return {
        mobile: '',
        password: ''
      }
    },
    methods: {
      login (e) {
        e.preventDefault()
        this.$http.post('http://jiancan.me/api/m1/merchants/login.json', { mobile: this.mobile, password: this.password }).then(function (response) {
          this.signin(response.data)
        }, function (response) {
          this.showAlert(response.data.error)
        })
      }
    }
  }
</script>



